<script>
  import { onMount } from "svelte";

  let imeTvrtke = "Tvrtka";
  let infoLinija1 = "...";
  let infoLinija2 = "...";
  let infoLinija3 = "...";
  let infoLinija4 = "...";
  let qrLink = "https://google.com";
  let logoTvrtke;
  let logoImg;
  let drawVizitkaFront;
  let drawVizitkaBack;
  let drawPhoneScreen;
  let qr;
  let vizitkaFront;
  let vizitkaBack;
  let phoneScreen;
  let vizitkaFrontContext;
  let vizitkaBackContext;
  let phoneScreenContext;
  let scale = 1;

  const getCanvasAsImage = (ctx, x, y, width, height) => {
    let imgData = ctx.getImageData(x, y, width, height);

    let tc = document.createElement("canvas");
    tc.width = imgData.width;
    tc.height = imgData.height;
    let tctx = tc.getContext("2d");
    tctx.putImageData(imgData, 0, 0);
    return tc.toDataURL("image/png");
  };

  const downloadURI = (uri, name) => {
    let link = document.createElement("a");
    link.download = name;
    link.href = uri;
    link.click();
  };

  const osvjezi = () => {
    qr = new VanillaQR({
      url: qrLink,
      width: 200,
      height: 200,
      colorLight: "#fafafa",
      colorDark: "#000",
      toTable: false,
      ecclevel: 4,
      noBorder: true,
    });
    let imageElement = qr.toImage("png");

    document.querySelector("#qrcode").innerHTML = "";
    document.querySelector("#qrcode").appendChild(imageElement);

    if (logoTvrtke.files[0] != null) {
      logoImg.src = URL.createObjectURL(logoTvrtke.files[0]);
    }

    setTimeout(() => {
      drawVizitkaFront();
      drawVizitkaBack();
      drawPhoneScreen();
    }, 200);
  };

  onMount(() => {
    vizitkaFront = document.querySelector("#vizitkaFront");
    vizitkaBack = document.querySelector("#vizitkaBack");
    phoneScreen = document.querySelector("#phoneScreen");

    vizitkaFrontContext = vizitkaFront.getContext("2d");
    vizitkaBackContext = vizitkaBack.getContext("2d");
    phoneScreenContext = phoneScreen.getContext("2d");

    drawVizitkaFront = () => {
      vizitkaFrontContext.clearRect(0, 0, 525 * scale, 300 * scale);

      // Background
      vizitkaFrontContext.fillStyle = "#fff";
      vizitkaFrontContext.fillRect(300 * scale, 0, 525 * scale, 300 * scale);

      // QR background
      vizitkaFrontContext.fillStyle = "#fafafa";
      vizitkaFrontContext.fillRect(0, 0, 300 * scale, 300 * scale);

      let image = document.querySelector("#qrcode img");
      vizitkaFrontContext.drawImage(
        image,
        50 * scale,
        50 * scale,
        200 * scale,
        200 * scale
      );
      vizitkaFrontContext.globalCompositeOperation = "source-over";

      // Ime tvrtke

      vizitkaFrontContext.font = `bold ${22 * scale}px Inter`;
      vizitkaFrontContext.fillStyle = "#3d3d3d";
      vizitkaFrontContext.fillText(imeTvrtke, 320 * scale, 60 * scale);

      // Info linije

      vizitkaFrontContext.font = `${16 * scale}px Inter`;
      vizitkaFrontContext.fillStyle = "#555";
      vizitkaFrontContext.fillText(infoLinija1, 320 * scale, 84 * scale);
      vizitkaFrontContext.fillText(infoLinija2, 320 * scale, 106 * scale);
      vizitkaFrontContext.fillText(infoLinija3, 320 * scale, 128 * scale);
      vizitkaFrontContext.fillText(infoLinija4, 320 * scale, 150 * scale);

      // Skeniraj za više info

      vizitkaFrontContext.fillText(
        "Skenirajte QR kôd",
        320 * scale,
        240 * scale
      );
      vizitkaFrontContext.fillText(
        "za više informacija",
        320 * scale,
        262 * scale
      );

      // NFC logo

      let nfcImg = document.querySelector("#nfclogo");
      vizitkaFrontContext.drawImage(
        nfcImg,
        490 * scale,
        15 * scale,
        21 * scale,
        28 * scale
      );
    };

    drawVizitkaBack = () => {
      vizitkaBackContext.clearRect(
        0 * scale,
        0 * scale,
        525 * scale,
        300 * scale
      );
      //   Gradient
      const grd = vizitkaBackContext.createLinearGradient(
        0 * scale,
        0 * scale,
        525 * scale,
        300 * scale
      );
      grd.addColorStop(0, "#fff");
      grd.addColorStop(1, "#eee");

      vizitkaBackContext.fillStyle = grd;
      vizitkaBackContext.fillRect(
        0 * scale,
        0 * scale,
        525 * scale,
        300 * scale
      );

      vizitkaBackContext.globalCompositeOperation = "source-over";

      // Text

      vizitkaBackContext.font = `${16 * scale}px Inter`;
      vizitkaBackContext.fillStyle = "#707070";
      vizitkaBackContext.fillText("powered by", 20 * scale, 276 * scale);

      // SocialByte logo

      vizitkaBackContext.font = `${16 * scale}px Oxygen`;
      vizitkaBackContext.fillStyle = "#000";
      vizitkaBackContext.fillText("Social", 112 * scale, 276 * scale);

      const grd2 = vizitkaBackContext.createLinearGradient(
        158 * scale,
        264 * scale,
        194 * scale,
        282 * scale
      );
      grd2.addColorStop(0, "#81A4F2");
      grd2.addColorStop(1, "#4568B6");

      vizitkaBackContext.font = `${16 * scale}px Oxygen`;
      vizitkaBackContext.fillStyle = grd2;
      vizitkaBackContext.fillText("Byte", 158 * scale, 276 * scale);
    };

    drawPhoneScreen = () => {
      phoneScreenContext.clearRect(
        0 * scale,
        0 * scale,
        368 * scale,
        776 * scale
      );
      // Background
      phoneScreenContext.fillStyle = "#fff";
      phoneScreenContext.fillRect(
        0 * scale,
        0 * scale,
        368 * scale,
        776 * scale
      );

      vizitkaBackContext.globalCompositeOperation = "source-over";

      // Logo tvrtke

      if (logoTvrtke.files[0] == null) {
        phoneScreenContext.fillStyle = "#f0f0f0";
        phoneScreenContext
          .roundRect(
            114 * scale,
            80 * scale,
            140 * scale,
            140 * scale,
            10 * scale
          )
          .fill();

        phoneScreenContext.font = `${18 * scale}px Inter`;
        phoneScreenContext.fillStyle = "#707070";
        phoneScreenContext.textAlign = "center";
        phoneScreenContext.fillText("Logo", 184 * scale, 148 * scale);
        phoneScreenContext.fillText("tvrtke", 184 * scale, 170 * scale);
      } else {
        let img = document.querySelector("#logoTvrtke");
        phoneScreenContext.save();
        roundedImage(
          114 * scale,
          80 * scale,
          140 * scale,
          140 * scale,
          10 * scale
        );
        phoneScreenContext.clip();
        phoneScreenContext.drawImage(
          img,
          114 * scale,
          80 * scale,
          140 * scale,
          140 * scale
        );
        phoneScreenContext.restore();
      }

      // Prislonite uređaj ovdje

      phoneScreenContext.font = `${32 * scale}px Inter`;
      phoneScreenContext.fillStyle = "#707070";
      phoneScreenContext.textAlign = "center";
      phoneScreenContext.fillText(
        "Prislonite uređaj",
        184 * scale,
        318 * scale
      );
      phoneScreenContext.font = `bold ${32 * scale}px Inter`;
      phoneScreenContext.fillText("ovdje", 184 * scale, 360 * scale);

      // QR

      let qrimage = document.querySelector("#qrcode img");
      phoneScreenContext.drawImage(
        qrimage,
        99 * scale,
        430 * scale,
        170 * scale,
        170 * scale
      );

      // ili skenirajte QR kod
      phoneScreenContext.font = `${20 * scale}px Inter`;
      phoneScreenContext.fillText("ili skenirajte", 184 * scale, 634 * scale);
      phoneScreenContext.font = `bold ${20 * scale}px Inter`;
      phoneScreenContext.fillText("QR kôd", 184 * scale, 660 * scale);

      // powered by socialbyte

      phoneScreenContext.textAlign = "left";
      phoneScreenContext.font = `${16 * scale}px Inter`;
      phoneScreenContext.fillStyle = "#707070";
      phoneScreenContext.fillText("powered by", 100 * scale, 740 * scale);

      phoneScreenContext.font = `${16 * scale}px Oxygen`;
      phoneScreenContext.fillStyle = "#000";
      phoneScreenContext.fillText("Social", 192 * scale, 740 * scale);

      const grd2 = phoneScreenContext.createLinearGradient(
        158 * scale,
        264 * scale,
        194 * scale,
        282 * scale
      );
      grd2.addColorStop(0, "#81A4F2");
      grd2.addColorStop(1, "#4568B6");

      phoneScreenContext.font = `${16 * scale}px Oxygen`;
      phoneScreenContext.fillStyle = grd2;
      phoneScreenContext.fillText("Byte", 238 * scale, 740 * scale);
    };

    osvjezi();

    const roundedImage = (x, y, width, height, radius) => {
      phoneScreenContext.beginPath();
      phoneScreenContext.moveTo(x + radius, y);
      phoneScreenContext.lineTo(x + width - radius, y);
      phoneScreenContext.quadraticCurveTo(x + width, y, x + width, y + radius);
      phoneScreenContext.lineTo(x + width, y + height - radius);
      phoneScreenContext.quadraticCurveTo(
        x + width,
        y + height,
        x + width - radius,
        y + height
      );
      phoneScreenContext.lineTo(x + radius, y + height);
      phoneScreenContext.quadraticCurveTo(
        x,
        y + height,
        x,
        y + height - radius
      );
      phoneScreenContext.lineTo(x, y + radius);
      phoneScreenContext.quadraticCurveTo(x, y, x + radius, y);
      phoneScreenContext.closePath();
    };

    CanvasRenderingContext2D.prototype.roundRect = function (x, y, w, h, r) {
      if (w < 2 * r) r = w / 2;
      if (h < 2 * r) r = h / 2;
      this.beginPath();
      this.moveTo(x + r, y);
      this.arcTo(x + w, y, x + w, y + h, r);
      this.arcTo(x + w, y + h, x, y + h, r);
      this.arcTo(x, y + h, x, y, r);
      this.arcTo(x, y, x + w, y, r);
      this.closePath();
      return this;
    };
  });
</script>

