<div class="controls">

  <label for="downloadbtn">
    <button
      id="downloadbtn"
      on:click={() => {
        downloadURI(getCanvasAsImage(vizitkaFrontContext, 0, 0, 525 * scale, 300 * scale), 'vizitka-prednja.png');
        downloadURI(getCanvasAsImage(vizitkaBackContext, 0, 0, 525 * scale, 300 * scale), 'vizitka-straznja.png');
        downloadURI(getCanvasAsImage(phoneScreenContext, 0, 0, 368 * scale, 776 * scale), 'mobitel.png');
      }}>
      Preuzmi .png
    </button>
  </label>
  <label for="scale">
    Uvećanje: {scale}x
    <input
      type="range"
      min="1"
      max="5"
      step="0.5"
      id="scale"
      bind:value={scale}
      on:change={osvjezi} />

  </label>
</div>

<canvas id="vizitkaFront" width={525 * scale} height={300 * scale} />
<canvas id="vizitkaBack" width={525 * scale} height={300 * scale} />
<canvas id="phoneScreen" width={368 * scale} height={776 * scale} />

<img id="logoTvrtke" bind:this={logoImg} alt="Logo" />

<img id="nfclogo" src="nfc.svg" alt="NFC" />

<div id="qrcode" />
